/* projects.css */

.projects-section {
    padding: 50px;
}

.projects-box {
    padding: 20px;
    text-align: center;
    background-color: transparent;
    max-width: 800px; /* Set a max-width for the box */
    margin: 0 auto; /* Center the box */
}

.projects-title {
    color: black;
    font-weight: bold;
    font-size: 50px;
    margin-bottom: 20px;
}

.project-images {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; /* Allow projects to wrap to the next line */
    gap: 20px; /* Adjust spacing between projects */
}

.project-image {
    width: 300px; /* Set the width of each image */
    height: auto; /* Preserve aspect ratio */
    margin: 0 10px; /* Adjust spacing between images */
    position: relative;
    border: 5px solid black;
    overflow: hidden;
}


.project-container {
    position: relative;
}

.image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

.image-container:hover .image-overlay {
    opacity: 1;
}

.project-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.project-image:hover .image-overlay {
    opacity: 1;
}

.project-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.github-link {
    color: black;
    margin-top: 20px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center; /* Center the content horizontally */
}

.github-link a {
    margin-left: 5px;
}

.github-icon {
    width: 30px; /* Set the width of the GitHub icon */
    height: auto; /* Preserve aspect ratio */
}
