body, html {
    margin: 0;
    padding: 0;
}

#home {
    height: calc(100vh - 8rem);
    width: 100vw;
    max-width: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; /* Stack items vertically for small screens */
}

video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.profile {
    object-fit: cover;
    height: 90vh;
    margin-top: 2rem; /* Adjust top margin for spacing */
    margin-bottom: 0; /* No bottom margin */
}

.introContent {
    color: black;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    text-align: center; /* Center text in the middle */
    font-style: italic;
}

.introName {
    color: #2a52be;
    font-size: 4.0rem;
    font-style: italic;
}

.introPara {
    font-size: medium;
    font-weight: 300;
    font-style: italic;
    letter-spacing: 1px;
}

.btn {
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
    border: 2px solid black;
}

.btn:hover {
    color: #2a52be;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #2a52be;
}

.resume {
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

.contactIcons {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.contactIcon {
    width: 5rem; /* Adjust the size as needed */
    height: 5rem;
    margin: 0 0.5rem; /* Adjust the spacing between icons */
}

@media screen and (max-width: 840px) {
    .profile {
        right: 0; /* Reset the right positioning */
    }
    .introContent {
        font-size: 10vw;
    }
    .hello {
        font-size: 4.5vw;
    }
}

@media screen and (max-width: 480px) {
    .profile {
        right: 0; /* Reset the right positioning */
    }
}

