/* navbar.css */
body {
    margin: 0;
    padding: 0;
  }

.navbar {
    height: 7rem;
    width: 100vw;
    max-width: none;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between; /* Updated to space-between */
    position: sticky;
    top: 0;
    z-index: 3;
    background: black;
}

.logo {
    object-fit: cover;
    height: 6rem;
    width: 6rem;
}

.desktopMenu {
    display: flex;
    align-items: stretch;
    justify-content: flex-end; /* Updated to flex-end to align items to the end */
}

.desktopMenuListItem {
    margin: 0 1rem;
    cursor: pointer;
}

.desktopMenuBox {
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
    border-radius: 0.3rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.desktopMenuBox:hover {
    background-color: #f0f0f0;
}

.desktopMenuImg {
    height: 1.5rem;
    width: 1.5rem;
    margin-right: 0.5rem;
}

.navbarTitle {
    font-family: 'cursive';
    font-weight: bold;
    font-size: 1.5rem; /* Adjust the font size as needed */
    color: white;
    margin: 0; /* Remove default margin */
    padding: 0 1rem; /* Add padding for spacing */
    text-align: center;
}

.hamburger {
    height: 6rem; /* Adjust the height */
    width: 6rem;  /* Adjust the width */
    cursor: pointer;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 10; /* Make sure the overlay is above other elements */
}

.overlay a {
    color: black;
    font-size: 1.5rem;
    text-decoration: none;
    margin: 1rem;
}

.overlay a:hover {
    text-decoration: underline;
}

.overlayMenu {
    flex-direction: column; /* Stack vertically */
}